import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {  useContext } from "react";

//components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { makeStyles } from "@material-ui/core/styles";
import Carousel from "react-bootstrap/Carousel";
//dependencies
import VideosItem from "./videosItem";
import VideosCategoriaFila from "./videosCategoriaFila";
import * as HistorialUserServer from "../../services/historialUser";
import * as PreferenciasUserServer from "../../services/preferenciasUser";
import Context from "../context/UserContext";
import VideoListSearch from "./videoListSearch";
import "../../styles/styles.css"

const useStyles = makeStyles((theme) => ({
  root: {
    margin: "auto",
  },
  "@media (max-width: 1248px)": {
    root: {
      display: "flex",
    },
  },
  "@media (max-width: 768px)": {
    root: {
    },
  },
 
}));

/**
 * Componente para organizar en el index los videos en su respectiva categoria, con un buscador y filtro
 * @param {*} videos
 * @param {*} categories
 * @returns Contenedor con los videos y categorias 
 */
function VideosListUser({videos, categories, ...props}) {
  const history = useNavigate();

  const [histUsers, setHistUsers] = useState(null);
  const [prefUsers, setPrefUsers] = useState(null); 
  const [categoriasTotales, setCategoriasTotales] = useState(null);
  const { user } = useContext(Context)

/**
 * FunciÃ³n para obtener el historial de los usuarios.
 */
  const getHistorialUsers = async () => {
    try {
      const res = await HistorialUserServer.ListHistorialUser({'user_id':user.id});
      setHistUsers(res);
      console.log(res);
    } catch (error) {
      console.log(error);
    }
  };
/**
 * FunciÃ³n para obtener las preferencias de los usuarios
 */
  const getPreferenciasUser = async () => {
    try {
      const res = await PreferenciasUserServer.ListPreferenciaUser({'user_id':user.id});   
      setPrefUsers(res);
      console.log(res);
    } catch (error) {
      console.log(error);
    }
  };
  
  useEffect(() => {
    getHistorialUsers();
    getPreferenciasUser();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  /**
   * FunciÃ³n para organizar las categorias de videos dependiendo de las preferencias.
   */
  useEffect(() => {
    if(prefUsers && categories) {
      let array = [];
      let array1 =[];
      let array2 = [];

      for (let i = 0; i < prefUsers.length; i++) {
        array1.push(prefUsers[i].categoria);        
      } 
      for (let i = 0; i < categories.length; i++) {
        array2.push(categories[i]);        
      }    
      
      for (var i = 0; i < array2.length; i++) {
          var igual=false;
           for (var j = 0; j < array1.length & !igual; j++) {
               if(array2[i].categoria === array1[j] )
                igual=true;
              }
          array = prefUsers
          if(!igual) array.push(array2[i]);
        }
        
      if (array) {
        //aqui se soluciona esto toca hacer que se coloque el prefUser al inicio y no al final
        
        setCategoriasTotales(array);
      }
      else {
        setCategoriasTotales(null);
      }

    }
  },[categories, prefUsers])

  /**
   * FunciÃ³n para redireccionar al reproductor del video seleccionado, se crea un nuevo historial de usuario si es la primera vez en ver el video, de lo contrario se envÃ­a este historial como state
   * @param {*} id 
   */
  const verVideo = async (id) =>{
    const formData = new FormData();
    let histUser;
    let contador = 0;
    formData.append("usuario", user.id);
    formData.append("video", id);
    try {
      for (let index = 0; index < histUsers.length; index++) {
      const element = histUsers[index];
        if (element.usuario.id === user.id && element.video === id) {
          histUser= element;
          contador++;
        }
      }
      if(contador ===0) {
        const hu = await  HistorialUserServer.RegisterHistorialUser(formData);
        histUser = hu.data;
      }      
    } catch (error) {
      console.log(error);
    }
    history(`/seeVideo/${id}`,{state:histUser});
  }
  console.log(categoriasTotales);
  const classes = useStyles();
  return (
    <div>
      <div id="carousel_videos" className=" ">
       {/*  <h2 className="pb-2">ðŸ•‘ Ãšltimos videos</h2> */}
        {/* videos carousel usuario */}
        {/* aca se coloca los ultimos videos traidos del histVisdeoUser */}
        <Carousel  className={classes.root} id="carousel" >
          {videos &&
            props.search(videos).slice(0, 6).map((video,index) => (
              <Carousel.Item key={index}>
                <VideosItem
                  key={video.id}
                  video={video}
                  listVideos={props.listVideos}
                  histUsers = {histUsers}
                  user = {user}
                  verVideo = {verVideo}
                />
              </Carousel.Item>
            ) )}
        </Carousel>
      </div>
     <br></br>
     <br></br>
      
      <div id="card_videos" className="mt-5"   > 
      
        {videos && prefUsers ? 
          <div className="">
            {/* videos por categorias usuario */}
              {            
              categoriasTotales && props.search2(categoriasTotales).map((element, index) => (
                <>
                { props.search2(categoriasTotales).length <= 1 ?
                  
                <VideoListSearch 
                  key= {index}
                  categoria ={element}
                  videos={videos}
                  search={props.search}
                  histUsers = {histUsers}
                  user = {user}
                  verVideo= {verVideo}
                >
                </VideoListSearch>
                :
                <VideosCategoriaFila
                  key= {index}
                  categoria ={element}
                  videos={videos}
                  search={props.search}
                  histUsers = {histUsers}
                  user = {user}
                  verVideo= {verVideo}  
                  >
                </VideosCategoriaFila>
                }   
                  </>
                ))
              }
          </div>
        :
        props.search2(categories).map((element,index) => (
          <div key={index} >
            {props.search2(categories).length <= 1 ?
            <VideoListSearch 
            key= {index}
            categoria ={element}
            videos={videos}
            search={props.search}
            histUsers = {histUsers}
            user = {user}
            verVideo= {verVideo}
            >

            </VideoListSearch>
            :
              <VideosCategoriaFila
                key= {index}
                categoria ={element}
                videos={videos}
                search={props.search}
                histUsers = {histUsers}
                user = {user}
                verVideo= {verVideo}
              >
              </VideosCategoriaFila>
            }
              </div>
          ))
        }
      </div>
    </div>
  );
}

export default VideosListUser;
